/*
 *  Vars for editor 3 app
 */
.Form-InputFill.is-disabled {
  border-color: rgba(46, 60, 67, 0.08);
  background: #F9F9F9;
  cursor: default; }

.Form-InputFill {
  width: 100%; }

.Editor-FormDialog .CustomList {
  display: block;
  position: relative;
  top: auto;
  right: auto;
  border: none;
  border-radius: 0;
  box-shadow: none; }

.Editor-FormDialog.Editor-boxModal {
  position: absolute;
  z-index: 100;
  top: 72px;
  right: 0;
  width: 248px; }

.Editor-fillContainer {
  width: 100%;
  height: 100%;
  padding: 0 8px;
  text-align: left; }

.Editor-fillContainer.Editor-fillContainer--ByValue {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .Editor-fillContainer.Editor-fillContainer--ByValue .Editor-fillContainer--Column {
    width: auto;
    margin-right: 10px;
    line-height: 30px; }
  .Editor-fillContainer.Editor-fillContainer--ByValue .Editor-fillContainer--ColorBarContainer {
    position: relative;
    width: 100%;
    max-width: 40px; }
    .Editor-fillContainer.Editor-fillContainer--ByValue .Editor-fillContainer--ColorBarContainer::after {
      content: "";
      position: absolute;
      top: -4px;
      left: -8px;
      width: 1px;
      height: 16px;
      border-left: 0.5px solid #CCC; }

.Editor-fillImage {
  display: flex;
  height: 16px;
  margin: 8px; }

.Editor-fillImageAsset {
  width: auto;
  height: 16px; }

.Editor-categoryImagesTag {
  padding: 2px;
  border: 1px solid #E4E4E4;
  border-radius: 3px;
  text-align: center; }

.CDB-OptionInput-item.is-disabled .CDB-OptionInput-content {
  color: #CCC; }

.CDB-OptionInput-item--categories .InputColor {
  padding: 0; }

.CDB-ColorBar.is-link {
  cursor: pointer; }

.CDB-OptionInput-container {
  height: 100%; }
  .CDB-OptionInput-container .CDB-OptionInput-item:last-child {
    width: auto; }
  .CDB-OptionInput-container .CDB-OptionInput-item:first-child {
    width: 100%; }

.CDB-OptionInput-container--noSlider {
  width: 100%; }

.InputColor-modalHeader {
  display: flex;
  justify-content: space-between; }

.InputColorCategory-content.is-hidden {
  display: none; }

.InputColorCategory-loader {
  display: flex;
  justify-content: center;
  padding: 16px; }

.InputColorCategory-loader.is-hidden {
  display: none; }

.CustomRamp {
  display: flex;
  justify-content: space-between; }

.CustomRamp-list {
  border-top: 1px solid #DDD; }

.CustomRamp .CustomRamp-listOptions {
  flex: 1; }

.CustomRamp-clear {
  padding: 12px 12px 12px 0;
  color: #1785FB; }

.RampItem-bar {
  width: 20px; }

.RampItem-text {
  max-width: calc(100% - 30px); }

.RampItem-secondaryContainer {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.RampItem-img {
  min-width: 22px;
  margin-left: 10px;
  text-align: center; }

