  margin-bottom: 8px;
  border-radius: 4px;
  cursor: pointer; }
  .Mosaic-item::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: 1px solid rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    pointer-events: none; }
