  .Mosaic-item:hover .Mosaic-remove {
    opacity: 1; }
  .Mosaic-item.is-selected::before {
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    border: 2px solid #2E3C43; }

